import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import upload from '@/components/upload'
import contrast from '@/components/contrast'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/upload',
      name: 'upload',
      component: HelloWorld,
      children: [{
        path: '/upload',
        name: 'upload',
        component: upload
      }]
    },
    {
      path: '/contrast',
      name: 'contrast',
      component: HelloWorld,
      children: [{
        path: '/contrast',
        name: 'contrast',
        component: contrast
      }]
    }

  ]
})



