<template>
  <div v-show="disableValue" style="margin-left: 50px" class="container">
    <el-row>
      <el-col style="text-align: center">
        切换位置:<el-select v-model="value1" placeholder="请选择">
          <el-option
            v-for="item in options1"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="value2" placeholder="请选择">
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary"  @click="jump()" >确定</el-button>
      </el-col>
    </el-row>
    <el-row style="margin-top: 80px">
      <el-col :span="10">
        <div  style="text-align:center">
          <img class="exam" style="width: 300px;height: 300px" :src="image1"/>
        </div>
      </el-col>
        <el-col style="text-align:center" :span="4">
          <div style="margin-top: 10px">
            <el-button :type="bt1"  @click="check(5)" round>明显强于</el-button>
          </div>
          <div style="margin-top: 10px">
            <el-button :type="bt2" @click="check(3)" round>略微强于</el-button>
          </div>
          <div style="margin-top: 10px">
            <el-button :type="bt3"  @click="check(1)" round>&nbsp;&nbsp;相&nbsp;&nbsp;&nbsp;等&nbsp;&nbsp;</el-button>
          </div>
          <div style="margin-top: 10px">
            <el-button :type="bt4"  @click="check(-3)" round>略微弱于</el-button>
          </div>
          <div style="margin-top: 10px">
            <el-button :type="bt5"  @click="check(-5)" round>略微明显</el-button>
          </div>
        </el-col>
        <el-col :span="10">
          <div style="text-align:center">
            <img  class="exam" style="width: 300px;height: 300px" :src="image2" />
          </div>
        </el-col>
    </el-row>
    <el-row>
      <div style="margin-top: 10px;text-align: center">
        <el-button  @click="previous()">上一个</el-button>
        <el-button  @click="next()">{{ nextName }}</el-button>
      </div>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      min: '',
      verify: 0,
      arr: [1,2],
      size: 0,
      image1: '',
      image2: '',
      bt1: 'primary',
      bt2: 'primary',
      bt3: 'primary',
      bt4: 'primary',
      bt5: 'primary',
      disableValue: false,
      nextName: '下一个',
      options1: [],
      value1: '',
      options2: [],
      value2: '',
      excelValue: ''
    }
  },
  methods: {
    primary(value){
      this.bt1 = 'primary'
      this.bt2 = 'primary'
      this.bt3 = 'primary'
      this.bt4 = 'primary'
      this.bt5 = 'primary'
      if (value === 5) {
        this.bt1 = ''
        this.verify = 1
      } else if (value === 3) {
        this.bt2 = ''
        this.verify = 1
      } else if (value === 1) {
        this.bt3 = ''
        this.verify = 1
      } else if (value === -3) {
        this.bt4 = ''
        this.verify = 1
      } else if (value === -5) {
        this.bt5 = ''
        this.verify = 1
      }
    },
    getV(arr){
      const that = this
      return new Promise(function(resolve, reject) {
        that.$http
          .post('getValue/' + arr[0] + '/' + arr[1])
          .then(res => {
            that.excelValue = res.data
            resolve()
          })
          .catch(error => {
            console.log(error)
          })
      })
    },
    jump(){
      const arr = [this.size - 1, this.size]

      this.getV(arr).then(res => {
        if (this.excelValue !== 0 && this.excelValue !== '') {
          if (this.value1 === '' || this.value2 ===''){
            this.$message({
              showClose: true,
              message: '请选择位置'
            })
          } else if (this.value1 === this.value2) {
            this.$message({
              showClose: true,
              message: '不能选择相同的位置'
            })
          } else {
            this.arr[0] = this.value1 > this.value2 ? this.value2 : this.value1
            this.arr[1] = this.value1 < this.value2 ? this.value2 : this.value1
            this.getValue(this.arr)
            this.getImage(this.arr[0], '1')
            this.getImage(this.arr[1], '2')
            this.nextName = '下一个'
          }
        } else {
          this.$message({
            showClose: true,
            message: '请标记完全部在进行切换'
          })
        }
      })


    },
    check(value) {
      this.primary(value)
      this.$http
        .post('update_excel/' + this.arr[0] + '/' + this.arr[1] + '/' + value)
        .then(res => {
          this.verify = 1
        })
        .catch(error => {
          console.log(error)
        })
    },
    previous () {
      this.nextName = '下一个'
      if (this.arr[1] - 1 !== 1) {
        this.arr[1] = this.arr[1] - 1
        if (this.arr[1] <= this.arr[0]) {
          this.arr[0] = this.arr[0] - 1
          this.arr[1] = this.size
        }
        this.getValue(this.arr)
        this.getImage(this.arr[0], '1')
        this.getImage(this.arr[1], '2')
      }
    },
    next () {
      if (this.verify > 0) {
        this.primary(6)
        if ((this.arr[0] === this.size - 2 && this.arr[1] === this.size) ||
          (this.arr[0] === this.size - 1 && this.arr[1] === this.size)) {
          this.nextName = '提交'
        } else {
          this.nextName = '下一个'
        }

        if (!(this.arr[0] === this.size - 1 && this.arr[1] === this.size)) {
          this.arr[1] = this.arr[1] + 1
          if (this.arr[1] > this.size) {
            this.arr[0] = this.arr[0] + 1
            this.arr[1] = this.arr[0] + 1
          }
          this.getValue(this.arr)
          this.getImage(this.arr[0], '1')
          this.getImage(this.arr[1], '2')
        } else {
          this.$message('提交成功')
        }
        this.verify = 0
      } else {
        this.$message('请先选择')
      }
    },
    getValue(arr){
      this.$http
        .post('getValue/' + arr[0] + '/' + arr[1])
        .then(res => {
          this.primary(parseInt(res.data))
        })
        .catch(error => {
          console.log(error)
        })
    },
    getImage(value, id) {
      this.$http
        .post('open/' + value)
        .then(res => {
          if (id === '1') {
            this.image1 = res.data
          } else {
            this.image2 = res.data
          }

        })
        .catch(error => {
          console.log(error)
        })
    },
    getSize() {
      this.$http
        .post('page_list')
        .then(res => {
          this.size = res.data
          if (this.size === 2) {
            this.nextName = '提交'
          }
          if (res.data > 1) {
            this.disableValue = true
            for (let i = 1; i <= res.data; i++) {
              const item = {value: i, label: i}
              this.options1.push(item)
            }
            this.options2 = JSON.parse(JSON.stringify(this.options1))
          }

        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  created() {
    this.min = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight) - 34
    this.getSize()
    this.getValue(this.arr)
    this.getImage(this.arr[0], '1')
    this.getImage(this.arr[1], '2')
  }
}
</script>
<style>
  .exam {
    border-width: 1px;
    border-color: black;
    border-style: double;
  }
</style>



